<template>
  <div class="classdetails">
    <div>
      <Titletop title="分类">
        <router-link slot="l" :to="{ name: 'cation' }"></router-link>
        <router-link slot="r" to="/index"></router-link>
      </Titletop>
    </div>
    <div class="content">
      <div class="cname">
        <h6>{{ title }}</h6>
      </div>
      <div class="alllist" v-for="item of contentslist" :key="item.id" @click="$bus.$emit('details', item.id)">
        <div class="alllist-l">
          <img v-lazy="item.cover_image" />
        </div>
        <div class="alllist-r">
          <h6>{{ item.name }}</h6>
          <p>{{ item.intro }}</p>
          <span>作者:{{ item.author }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
export default {
  name: "Myclassdetails",
  components: { Titletop },
  data() {
    return {
      title: localStorage.getItem("title"),
      contentslist: [],
    };
  },
  mounted() {
    let a; 
    if (localStorage.getItem("param")) {
      a = localStorage
        .getItem("param")
        .replaceAll(",", "&")
        .replaceAll(":", "=")
        .replaceAll('"', "")
        .replaceAll("{", "")
        .replaceAll("}", "");
    } else {
      a = "";
    }
    this.axios.get("https://jyapi.jyacg.com/web/book_room?" + a).then((res) => {
      this.contentslist = res.data.data;
    });
  },
};
</script>

<style scoped lang="scss">
.content {
  width: 350px;
  padding: 0 12.5px;

  .cname {
    width: 100%;
    height: 33px;
    margin-bottom: 18px;
    h6 {
      width: 100%;
      height: 33px;
      background-color: #b98d7c;
      color: #fff;
      font-size: 14px;
      line-height: 33px;
      text-align: center;
      font-weight: bold;
      border-radius: 5px;
    }
  }
  .alllist {
    display: flex;
    padding-bottom: 15px;
    width: 350px;
    background-color: #fff;
    .alllist-l {
      img {
        width: 65px;
        height: 88px;
      }
    }
    .alllist-r {
      height: 100px;
      margin-left: 10px;
      width: 240px;
      h6,
      p,
      span {
        margin-bottom: 12px;
      }

      h6 {
        color: #333;
        font-size: 14px;
        font-weight: bold;
        margin-bottom: 12px;
      }
      p {
        height: 36px;
        color: #999;
        overflow: hidden;
        font-size: 12px;
        line-height: 18px;
      }
      span {
        font-size: 12px;
        color: #999;
      }
    }
  }
}
</style>